/*****************************
    Typography
*****************************/
body {
  font-family: "Outfit", sans-serif;
  font-style: normal;
  font-size: 16px;
  line-height: 1.5;
  color: #8d8c8c;
  overflow-x: hidden;
  font-weight: 300; }

a {
  outline: medium none !important;
  color: #2673f8;
  transition: all 0.3s ease-in-out;
  text-decoration: none !important; }
  a:focus {
    color: #2673f8;
    text-decoration: none !important; }
  a:hover {
    color: #2673f8;
    text-decoration: none !important; }

input {
  outline: medium none !important;
  color: #2673f8; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Outfit", sans-serif;
  font-weight: normal;
  color: #091c3a;
  margin-top: 0px;
  font-weight: 400; }
  h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
    color: inherit; }

label {
  font-weight: normal; }

h1 {
  font-size: 92px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.1; }

h2 {
  font-size: 48px;
  font-weight: 500; }

h3 {
  font-size: 24px;
  font-weight: 500; }

h4 {
  font-size: 22px;
  font-weight: 500; }

h5 {
  font-size: 20px;
  font-weight: 500; }

h6 {
  font-size: 18px;
  font-weight: 500; }

p {
  line-height: 1.5; }

*::-moz-selection {
  background: #2673f8;
  color: #ffffff;
  text-shadow: none; }

::-moz-selection {
  background: #2673f8;
  color: #ffffff;
  text-shadow: none; }

::selection {
  background: #2673f8;
  color: #ffffff;
  text-shadow: none; }

.fw-medium {
  font-weight: 500; }

.fw-600 {
  font-weight: 600; }

.border-radius {
  border-radius: 6px; }

.border-radius-sm {
  border-radius: 4px; }

.border-radius-md {
  border-radius: 10px; }

.border-radius-lg {
  border-radius: 50%; }

.box-shadow {
  box-shadow: 0px 1px 30px 0px rgba(9, 28, 58, 0.09); }

.box-shadow-sm {
  box-shadow: 0px 2px 5px 0px rgba(9, 28, 58, 0.1); }

.box-shadow-lg {
  box-shadow: 0 1rem 3rem rgba(9, 28, 58, 0.175); }

/* form-control */
.form-control, .form-select {
  border: 1px solid #ededed;
  border-radius: 0px;
  height: 60px;
  padding: 10px 20px;
  box-shadow: none;
  font-size: 14px;
  font-weight: 500;
  color: #091c3a;
  background-clip: inherit !important; }
  .form-control:focus, .form-select:focus {
    box-shadow: none;
    border-color: #2673f8; }

.avatar-lg {
  min-width: 80px;
  width: 80px; }

/* input-group-prepend */
.input-group-prepend .input-group-text, .input-group-append .input-group-text {
  width: 50px;
  background: transparent;
  border-color: #ededed;
  box-shadow: none;
  justify-content: center; }

/* file-upload */
.input-group.file-upload {
  height: 50px; }
  .input-group.file-upload .form-control {
    height: 50px;
    color: #8d8c8c;
    border-radius: 3px;
    border-color: #ededed;
    position: relative;
    z-index: 2;
    width: 100%;
    margin: 0;
    opacity: 0;
    border-radius: 3px; }
  .input-group.file-upload .input-group-text {
    height: 50px;
    font-size: 14px;
    font-weight: 600;
    color: #8d8c8c;
    border-color: #ededed;
    padding: 15px 90px 15px 20px;
    border-radius: 3px;
    transition: background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;
    line-height: 1.5;
    background-color: #ffffff;
    justify-content: space-between;
    margin-left: 0; }
    .input-group.file-upload .input-group-text:after {
      content: "Browse";
      height: 48px;
      padding: 14px 25px;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 3;
      display: block;
      line-height: 1.5;
      color: #091c3a;
      background-color: #f0f6fa;
      border-left: inherit;
      border-radius: 0px 3px 3px 0px; }

