/*****************************
  Button
*****************************/
.btn {
  border: none;
  z-index: 1;
  position: relative;
  font-size: 16px;
  padding: 15px 40px;
  overflow: hidden;
  border-radius: 0;
  transition: all 0.3s ease-in-out;
  font-weight: 500;
  display: inline-flex;
  align-items: center;
  text-transform: capitalize; }
  .btn:focus {
    box-shadow: none;
    outline: none; }
  .btn:active {
    box-shadow: none;
    outline: none; }

.btn.btn-link {
  padding: 0 !important; }

/*Btn Color*/
.btn.btn-primary {
  background: #2673f8;
  color: #ffffff; }
  .btn.btn-primary:hover {
    background: #091c3a;
    transform: translateY(-2px); }
  .btn.btn-primary:active {
    box-shadow: none; }
  .btn.btn-primary:focus {
    box-shadow: none; }

.btn-white {
  background: #ffffff;
  color: #000000; }
  .btn-white:hover {
    background: #2673f8;
    color: #ffffff;
    transform: translateY(-2px); }
  .btn-white:focus {
    color: #000000; }

.btn-secondary {
  background: #091c3a;
  color: #ffffff; }
  .btn-secondary:hover {
    background: #2673f8;
    transform: translateY(-2px); }

.bg-dark .btn-primary:hover {
  background: #ffffff;
  color: #2673f8; }

.btn.text-secondary:hover {
  color: #2673f8 !important; }

.btn.text-white:hover {
  color: #2673f8 !important; }

.btn.text-primary:hover {
  color: #091c3a !important; }

/*Btn Size*/
.btn.btn-lg {
  font-size: 18px;
  padding: 20px 40px; }

.btn.btn-md {
  font-size: 14px;
  padding: 15px 30px; }

.btn.btn-sm {
  font-size: 14px;
  padding: 12px 20px; }

/*btn-app*/
.btn-app {
  font-size: 14px;
  padding: 8px 20px;
  background-color: #091c3a;
  color: #ffffff;
  display: inline-flex;
  align-items: center;
  text-transform: capitalize; }
  .btn-app i {
    font-size: 30px;
    padding-right: 10px; }
  .btn-app .small-text {
    font-size: 11px; }
  .btn-app span {
    line-height: 14px; }
  .btn-app:hover {
    background-color: #2673f8;
    color: #ffffff;
    transform: translateY(-2px); }

@media (max-width: 575px) {
  .btn {
    font-size: 14px;
    padding: 12px 20px; }
  .btn-app {
    display: inline-flex;
    padding: 8px 14px; }
    .btn-app i {
      font-size: 24px;
      padding-right: 6px; } }

